<template>
    <section>
        <loader :is-visible="isLoading"></loader> 
        <tg-list :IsShowAction="false" :showCheckBox="false" :listType="['List']" ModuleName="Flagged_Screenshot"
            SubModuleCode="Flagged_Screenshot" IdentityColumn="ScreenShotId" HeaderText="Flagged Screenshots" :ListData="FlaggedScreenShotListing"
            :HeaderData="Headers" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :RenderRowActionMethod="[]"
            :ListDataCallBackFunction="FetchData" :LegendArray="[]" :SortExp="sortExp" :SortBy="sortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata">
        </tg-list>
        <ScreenshotDetails v-if="ShowScreenshotDetails" :ScreenshotDetailArray="ScreenshotDetailArray" :ScreenShotData="ScreenShotData"></ScreenshotDetails>
    </section>
</template>
<script>
import Schema from "../TimeTracking/FlaggedScreenshotSchema";
import DataService from '../../services/DataService';
import moment from 'moment';
import ScreenshotDetails from "../../components/Modal/modalScreenshotDetails.vue";
import { storeToRefs } from "pinia";
import { useloggedUserInfo } from "@/stores/UserInfo";
const { GetUserInfo } = storeToRefs(useloggedUserInfo());
export default {
    components:{
        ScreenshotDetails
    },
    data() {
        return {
            isLoading:false,
            ShowScreenshotDetails:false,
            leftSearchFields: Schema.leftsearchSchema,
            listheaderbuttons: Schema.buttons,
            FlaggedScreenShotListing:[],
            noRecord: true,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            sortBy: 'sortdate',
            sortExp: 'asc',
            pageSize: 10,
            pageNum: 1,
            Userid: 'User=0',
            projectId: 'projectId=0',
            modelinputdate: new Date().toISOString().split('T')[0],
            dateFrom:'',
            GetFromDate:'',
            GetToDate:'',
            Headers:[
                {
                    "COLUMN_NAME": 'screenshoT_TITLE',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ScreenshotTitle'),
                    "DISPLAY_ORDER": 1,
                },
                {
                    "COLUMN_NAME": 'trackDescription',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('TrackDescription'),
                    "DISPLAY_ORDER": 1,
                },
                {
                    "COLUMN_NAME": 'createdByName',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('UserName'),
                    "DISPLAY_ORDER": 1,
                },
                {
                    "COLUMN_NAME": 'projectName',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ProjectTaskFlagged'),
                    "DISPLAY_ORDER": 1,
                },
                {
                    "COLUMN_NAME": 'createD_AT',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ScreenShotCreatedAt'),
                    "DISPLAY_ORDER": 1,
                },
                {
                    "COLUMN_NAME": 'totalHours',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('TimeTracked'),
                    "DISPLAY_ORDER": 1,
                },
                {
                    "COLUMN_NAME": 'newStartDate',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Date'),
                    "DISPLAY_ORDER": 1,
                },
                {
                    "COLUMN_NAME": 'trackAgent',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('TrackAgent'),
                    "DISPLAY_ORDER": 1,
                },
                {
                    "COLUMN_NAME": 'activity',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ActivityFlagged'),
                    "DISPLAY_ORDER": 1,
                },
            ]
        }
    },
    created(){
        var vm = this;
        vm.FetchData();
        Schema.bindleftCommonSearchdropdown(this, GetUserInfo.value.UserType);
    },
    updated() {
        Schema.leftsearchSchema.forEach(item => {
            item.value = '';
            item.secondValue = '';
            item.isSearch = false;
        })
    },
    methods:{
        commonHeaderButtonClick(event){
            switch (event.callbackfunction) {
                case "BackToWorkDiaryListing":
                    this.BackToWorkDiaryListing();
                    break;
            }
        },
        FetchData(){
            var vm = this;
            vm.isLoading = true; 
            var url = `${vm.Userid}&${vm.projectId}&datefrom=${vm.GetFromDate}&dateto=${vm.GetToDate}&pageSize=${vm.pageSize}&pageNum=${vm.pageNum}&sortBy=${vm.sortBy}&orderBy=${vm.sortExp}`;
            DataService.GetFlagScreenshotListing(url).then(response=>{
                if(response.data.length > 0){
                    vm.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "screenshoT_TITLE") {
                            column.settings = {
                                clickEvent: vm.clickEventOfColumn,
                                formatter: vm.columnDataFormatter,
                            };
                        }
                    })
                    vm.FlaggedScreenShotListing = response.data;
                    vm.FlaggedScreenShotListing.forEach(item=>{
                        var Getresponse = vm.FlaggedScreenShotListing.find(x=> x.screenshoT_ID === item.screenshoT_ID)
                        Getresponse.projectName = item.projectName + '-' + item.taskname;
                        Getresponse.createD_AT = vm.FormatDateToTime(item.createD_AT);
                        Getresponse.totalHours = vm.secondsToTime(item.totalHours);
                        Getresponse.newStartDate = vm.$options.filters.formatDate(item.newStartDate);
                        Getresponse.activity = item.activity + ' %';
                    })
                    vm.TotalRecords = response.data[0].total;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.pageSize);
                    vm.CurrentPage = vm.pageNum
                    vm.isLoading = false;
                    vm.noRecord = false;
                }else{
                    vm.FlaggedScreenShotListing = [];
                    vm.TotalRecords = 0;
                    vm.isLoading = false;
                    vm.noRecord = true;
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        FormatDateToTime(Getdate) {
            return moment(Getdate).format('LT');
        },
        secondsToTime(secs){
            // TO Get Hours
            var hours = Math.floor(secs / (60 * 60));
            // To Get Minutes
            var divisor_for_minutes = secs % (60 * 60);
            var minutes = Math.floor(divisor_for_minutes / 60);
            // To Get Seconds
            var divisor_for_seconds = divisor_for_minutes % 60;
            var seconds = Math.ceil(divisor_for_seconds);

            var obj = hours + 'H ' + minutes + 'M ' + seconds + 'S';
            return obj;
        },
        pagerMethod(value){
            this.pageSize = value.PageSize;
            this.pageNum = value.PageNumber;
            this.FetchData();
        },
        clickEventOfColumn: function (row, column) {
            var vm = this;
            if (column.COLUMN_NAME == 'screenshoT_TITLE') {
                vm.ShowScreenshotDetails = true;
                vm.ScreenshotDetailArray = row;
                vm.ScreenShotData = {
                    'UserId': vm.Userid,
                    'ProjectId': vm.projectId,
                    'InputDate': row.dateStart
                }
            }
        },
        CloseScreenshotDetails(){
            var vm = this;
            vm.ShowScreenshotDetails = false;
        },
        leftCommonSearch(value){
            var vm = this;
            vm.GetFromDate = '';
            vm.GetToDate = '';
            if (value.searchConditionJson.includes('projectId')) {
                vm.projectId = value.searchConditionJson;
            }else {
                vm.Userid = value.searchConditionJson;
            }
            if(value.searchCondition.includes('DATE')){
                var GetToAndFromDate = value.searchCondition.split("'");
                GetToAndFromDate.forEach(Gdate=>{
                    var getdate = this.$options.filters.formatDate(Gdate);
                    if(getdate != 'Invalid date'){
                        if(vm.GetFromDate == ''){
                            vm.GetFromDate = getdate;
                        }else{
                            vm.GetToDate = getdate;
                        }
                    }
                })
            }               
            vm.FetchData();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.sortBy = s;
            if (s === this.sortBy) {
                if (this.sortExp == "") {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.sortExp == "ASC") {
                    this.sortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + "  span:first-child").attr("class", SortDir);
        },
        BackToWorkDiaryListing(){
            this.$router.push({path:'/WorkDiary/Index'});
        }
    }
}
</script>